const media_sizes = {
  tablet: 801,
  desktop: 801,
  desktopM: 1001,
  desktopL: 1201
}

const reactMediaQueries = {
  tablet: `(max-width: ${media_sizes['tablet']}px)`,
  desktop: `only screen and (min-width: ${media_sizes['desktop'] / 16}em)`,
  desktopL: `only screen and (min-width: ${media_sizes['desktopL'] / 16}em)`,
  deskShort: `only screen and (min-width: ${media_sizes['desktop'] / 16}em) and (max-height: ${media_sizes['desktop'] / 16}em)`,
  desktopSCap: `only screen and (min-width: ${media_sizes['desktop'] / 16}em) and (max-width: ${media_sizes['desktopM'] / 16}em)`,
  desktopLCap: `only screen and (min-width: ${media_sizes['desktopL'] / 16}em)`,
}

const mediaQueries = {
  tablet: `@media ${reactMediaQueries.tablet}`,
  desktop: `@media ${reactMediaQueries.desktop}`,
  desktopL: `@media ${reactMediaQueries.desktopL}`,
  deskShort: `@media ${reactMediaQueries.deskShort}`,
  desktopSCap: `@media ${reactMediaQueries.desktopSCap}`,
  desktopLCap: `@media ${reactMediaQueries.desktopLCap}`
}

